body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Add a blinking cursor to the end of the animated text */
@keyframes cursor {
  50% {
    border-color: transparent;
  }
}

.glass {
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.glass2 {
  background: rgba(255, 255, 255, 0.4);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(13px);
  -webkit-backdrop-filter: blur(13px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

#svg_curve {
  cursor: pointer;
  shape-rendering: crispEdges;
  fill: url(#MyGradient);
}

.aws-btn {
  --button-default-height: 45px;
  --button-default-font-size: 16px;
  --button-default-border-radius: 21px;
  --button-horizontal-padding: 25px;
  --button-raise-level: 4px;
  --button-hover-pressure: 1.25;
  --transform-speed: 0.15s;
  --button-primary-color: #36b3f7;
  --button-primary-color-dark: #1f74a1;
  --button-primary-color-light: #ffffff;
  --button-primary-color-hover: #2c9dda;
  --button-primary-color-active: #36bdf7;
  --button-primary-border: none;

  --button-secondary-color: #093f64;
  --button-secondary-color-dark: #072743;
  --button-secondary-color-light: #ffffff;
  --button-secondary-color-hover: #0d3252;
  --button-secondary-color-active: #0a2c49;
  --button-secondary-border: 2px solid #0c2a45;

  --button-anchor-color: #0e4f88;
  --button-anchor-color-dark: #072743;
  --button-anchor-color-light: #ffffff;
  --button-anchor-color-hover: #0d4a7f;
  --button-anchor-color-active: #0c4271;
  --button-anchor-border: none;

  --button-danger-color: #c42e2e;
  --button-danger-color-dark: #861f1f;
  --button-danger-color-light: #ffffff;
  --button-danger-color-hover: #bc2c2c;
  --button-danger-color-active: #af2929;
  --button-danger-border: none;
  }


